import React, { Fragment } from "react";
// import PropTypes from "prop-types";

import './TransactionHistory.css';

export default function TransactionHistory ({ items }) {
   
    return(
        <Fragment>
            <table className="transaction-history">
                <thead>
                    <tr>
                    <th>Type</th>
                    <th>Amount</th>
                    <th>Currency</th>
                    </tr>
                </thead>

                <tbody>
                {items.map(item => (
                        <tr key={item.id} className="item">
                            <td className="label">{item.type}</td>
                            <td className="label">{item.amount}</td>
                            <td className="percentage">{item.currency}</td>
                        </tr>
                ))}
                </tbody>
            </table>
        </Fragment>
    );
}

